.Supplement{
    display: flex;
    justify-content: center;
    column-gap: 20px;
}

.Supplement__container{
    background-image: url("../../assets/Rectangle (1).png");
    background-size: cover;
    background-repeat: no-repeat;
    height: 500px;
    width: 710px;
    margin-top: 16px;
}

.Laser__container{
    background-image: url("../../assets/Rectangle (2).png");
    background-size: cover;
    background-repeat: no-repeat;
    height: 500px;
    width: 710px;
    margin-top: 16px;
    margin-bottom: 16px;
}

.Supplement__text{
    display: flex;
    align-items: center;
    flex-direction: column;
    margin: 310px 143px;
    height: 160px;
}

.text{
    font-weight: 400;
    font-size: 34px;
    line-height: 43px;
    text-align: center;
    letter-spacing: 0.5px;
    color: #FFFFFF;
}

.Nine__text{
    font-weight: 400;
    font-size: 16px;
    line-height: 25px;
    text-align: center;
    color: #FFFFFF;
}

.One__text{
    font-weight: 400;
    font-size: 15px;
    line-height: 25px;
    text-align: center;
    color: #FFFFFF;
}