.Wellness{
    background-image: url("../../assets/Rectangle.png");
    background-size: cover;
    background-repeat: no-repeat;
    background-attachment: fixed;
    height: 800px;
}
.Wellness__container{
    display: flex;
    align-items: center;
    flex-direction: column;
    width: 900px;
    height: 300px;
    margin: 0 auto;
    padding-top: 215px;

}
.FUTURE__text{
    font-weight: 400;
    font-size: 57px;
    line-height: 67px;
    text-align: center;
    letter-spacing: 0.81px;
    color: #FFFFFF;
}
.balance__text{
    font-weight: 400;
    font-size: 16px;
    line-height: 25px;
    text-align: center;
    letter-spacing: 0.44px;
    color: #FFFFFF;
    margin-top: 33px;
}

.button__text{
    font-weight: 400;
    font-size: 14px;
    line-height: 20px;
    text-align: center;
    letter-spacing: 2px;
    color: #FFFFFF;
    border: 1px solid #FFFFFF;
    padding: 8px 48px 7px 49px;
    background: transparent;
}

.button__text2{
    font-weight: 400;
    font-size: 14px;
    line-height: 20px;
    text-align: center;
    letter-spacing: 2px;
    color: #FFFFFF;
    border: 1px solid #FFFFFF;
    padding: 8px 79px 7px 80px;
    background: transparent;
}


.button__block{
    display: flex;
    column-gap: 20px;
    margin-top: 60px;
}