.Meet__container{
    background-image: url("../../assets/Rectangle13.png");
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center;
    height: 430px;
    position: relative;
}

.founder__text{
    font-weight: 400;
    font-size: 35px;
    line-height: 43px;
    letter-spacing: 0.5px;
    color: #1B1B1B;
}

.story__text{
    font-weight: 400;
    font-size: 16px;
    line-height: 25px;
    color: #666666;
    margin-top: 33px;
    margin-bottom: 12px;
}
.more__text{
    font-weight: 400;
    font-size: 14px;
    line-height: 20px;
    letter-spacing: 2px;
    text-transform: uppercase;
    color: #1B1B1B;
    margin-top: 12px;
}

.READ__block{
    position: absolute;
    right: 250px;
    bottom: 150px;
}