.footer__container{
    display: flex;
    align-items: center;
    height: 433px;
    background: #000000;
}

.footer__block{
    display: flex;
}

.links__href{
    font-weight: 400;
    font-size: 12px;
    line-height: 15px;
    color: #7E7E7E;
    margin-top: 8px;
    display: flex;
    flex-direction: column;
}

.block__text{
    width: 167px;
}

.footer__text{
    font-weight: 400;
    font-size: 13px;
    line-height: 20px;
    color: #FFFFFF;
    margin-bottom: 32px;
}

.footer__Buy{
    font-weight: 400;
    font-size: 12px;
    line-height: 15px;
    color: #FFFFFF;
    margin-top: 8px;
}

.footer__text2{
    font-weight: 400;
    font-size: 13px;
    line-height: 20px;
    color: #FFFFFF;
    margin-bottom: 32px;
    display: flex;
    justify-content: right;
}

.links__href2{
    font-weight: 400;
    font-size: 12px;
    line-height: 15px;
    color: #7E7E7E;
    margin-top: 42px;
    margin-bottom: 15px;
    display: flex;
    justify-content: right;
}

.links__href3{
    font-weight: 400;
    font-size: 12px;
    line-height: 15px;
    color: #7E7E7E;
