.header__LASER{
    height: 65px;
}

.ACTIVATE__container{
    display: flex;
    justify-content: space-between;
    height: 44px;
    padding-top: 20px;
    margin: 0 auto;
}

.header__links{
    display: flex;
    align-items: center;
    column-gap: 32px;
}

.links__text{
    font-weight: 400;
    font-size: 10px;
    line-height: 12px;
    letter-spacing: 1.67px;
    color: rgba(0, 0, 0, 0.3);
    transition: all 0.2s;
}

.links__text:hover{
    color: black;
    border-bottom: 1px solid black;
    transition: 0.1s;
}

.BUY__block{
    display: flex;
    align-items: center;
    column-gap: 32px;
}

.LYMA__block{

}

.BUY__button{
    font-weight: 400;
    font-size: 10px;
    line-height: 12px;
    text-align: center;
    letter-spacing: 1.67px;
    color: #FFFFFF;
    background: #000000;
    border: 1px solid #000000;
    padding: 5px 18px 6px 18px;
}